import React from 'react';
import SearchBar from './SearchBar';
import axios from 'axios';
import '../css/weather.css';

class App extends React.Component{
  state = { location: null,
            weather: null, 
            temp: null, 
            speed: null, 
            humidity: null, 
            feels_like: null};

	componentDidMount(){
		this.onTermSubmit('san jose');
	}

	onTermSubmit = async (term) => {
    console.log(term);
    
		const response = await axios.get('https://weather-westcliff.herokuapp.com/testAPI',{
			params: {
				location: term
			}
		});
    console.log(response);
    
		this.setState({ 
      location: term,
      weather: response.data.weather.toLowerCase(), 
      temp: response.data.temp, 
      speed: response.data.speed, 
      humidity: response.data.humidity, 
      feels_like: response.data.feels_like
    });
    
	};
/*
	onVideoSelect = (video) => {
		this.setState({
			selectedVideo: video,
		});
	}*/

	render() {
		return (
			<div className={`ui container ${this.state.weather}-bg`}>
				<SearchBar onFormSubmit={this.onTermSubmit}/>
				<div className="ui grid">
					<div className="ui row">
						<div className="sixteen wide column">
              <h1>{this.state.location}</h1>
              <div className="w-container">
                <div className={this.state.weather}></div>
              </div>
              <p>Weather: {this.state.weather}</p>
              <p>Tempreture: {this.state.temp}</p>
              <p>Speed: {this.state.speed}</p>
              <p>Humidity: {this.state.humidity}</p>
              <p>Feels like: {this.state.feels_like}</p>  
						</div>

					</div>
				</div>
			</div>
			);
	}
}

export default App;
